<template>
  <p class="folder hide">
  <em>
    {{name}}
    <img src="typeIcons/folder.svg" alt="">
  </em>
  <template v-for="file in files">
      <FileView :name="file.name" :type="file.type" :length="file.length"/>
  </template>
  <template v-for="folder in folders">
    <FolderView :name="folder.name" :folders="folder.folders" :files="folder.files"/>
  </template>
  </p>
  
</template>

<script>
import FileView from "./FileView";
export default {
  name: 'FolderView',
  components: {FileView},
  props: {
    name: String,
    folders:Array,
    files:Array
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .folder{
    position: relative;
    color: blue;
    em {
      text-decoration: none;
      font-style: normal;
      cursor: pointer;
      img {
        width: 20px;
        height: 20px;
        position: absolute;
        left: -25px;
        top: -.5px;
      }
    }
    .folder {
      font-size: 17px;
      margin-left: 20px;
    }
    &.hide {
      .folder {
        display: none;
      }
      .file {
        display: none;
      }
    }
  }
</style>
